import React from 'react';

const Counter = ({count, onIncrementClick, onDecrementClick}) => {
    return <div>
        <p>{count}</p>
        <button onClick={onIncrementClick}>+</button>
        <button onClick={onDecrementClick}>-</button>
      </div>;
}

export default Counter


