import { createStore } from "redux"

function counter(state = {"count": 0}, action) {
  switch (action.type) {
    case "INCREMENT":
      return {count: state.count += 1};
    case "DECREMENT":
      return { count: state.count -= 1};
    default:
      return state;
  }
}

let store = createStore(counter);

export default store;


